// =============================================================================
// USER ONBOARDING API
// =============================================================================
// GET /api/v1/user/onboarding - Get onboarding status and checklist
// PUT /api/v1/user/onboarding - Mark onboarding step as complete

import { NextRequest, NextResponse } from 'next/server';
import { requireAuth } from '@/lib/auth';
import { getDb } from '@/db/client';

// Onboarding steps definition
const ONBOARDING_STEPS = [
  {
    id: 'account_created',
    title: 'Create your account',
    description: 'Sign up for NexFlow',
    autoComplete: true, // Completed on signup
  },
  {
    id: 'api_key_copied',
    title: 'Copy your API key',
    description: 'Save your API key somewhere secure',
    action: 'Copy the API key shown after signup',
  },
  {
    id: 'first_endpoint',
    title: 'Create your first endpoint',
    description: 'Set up a metered API endpoint',
    action: 'Go to Dashboard → Create Endpoint',
    link: '/dashboard',
    docs: '/docs?section=metered',
  },
  {
    id: 'test_request',
    title: 'Make a test request',
    description: 'Try calling your metered endpoint',
    action: 'Use curl or your app to call the endpoint',
    docs: '/docs?section=overview',
  },
  {
    id: 'view_usage',
    title: 'Check your usage',
    description: 'See your API calls in the Usage tab',
    action: 'Go to Dashboard → Usage',
    link: '/dashboard/usage',
  },
];

export async function GET(request: NextRequest) {
  const authResult = await requireAuth(request);
  if (authResult.response) {
    return authResult.response;
  }

  const { apiKey } = authResult;
  const accountId = apiKey.userId;

  if (!accountId) {
    return NextResponse.json(
      { error: 'No account associated with this API key', code: 'NO_ACCOUNT' },
      { status: 400 }
    );
  }

  try {
    const db = getDb();

    // Get account creation date
    const accountResult = await db.query<{ created_at: string; name: string | null }>(
      `SELECT created_at, name FROM accounts WHERE id = $1`,
      [accountId]
    );

    if (accountResult.rows.length === 0) {
      return NextResponse.json(
        { error: 'Account not found', code: 'ACCOUNT_NOT_FOUND' },
        { status: 404 }
      );
    }

    const account = accountResult.rows[0];
    const isNewUser = isWithinDays(account.created_at, 7); // New if < 7 days old

    // Get completed onboarding steps
    const stepsResult = await db.query<{ step_id: string; completed_at: string }>(
      `SELECT step_id, completed_at FROM onboarding_progress WHERE account_id = $1`,
      [accountId]
    );

    const completedSteps = new Map(
      stepsResult.rows.map((row) => [row.step_id, row.completed_at])
    );

    // Check for auto-complete conditions
    const autoCompleteChecks = await checkAutoCompleteConditions(db, accountId);

    // Build checklist with completion status
    const checklist = ONBOARDING_STEPS.map((step) => {
      const isAutoCompleted = autoCompleteChecks[step.id];
      const manuallyCompleted = completedSteps.has(step.id);
      const completed = isAutoCompleted || manuallyCompleted;

      return {
        ...step,
        completed,
        completedAt: completedSteps.get(step.id) || (isAutoCompleted ? 'auto' : null),
      };
    });

    const completedCount = checklist.filter((s) => s.completed).length;
    const totalSteps = checklist.length;
    const progressPercent = Math.round((completedCount / totalSteps) * 100);
    const allComplete = completedCount === totalSteps;

    return NextResponse.json({
      success: true,
      onboarding: {
        isNewUser,
        showChecklist: isNewUser && !allComplete,
        progress: {
          completed: completedCount,
          total: totalSteps,
          percent: progressPercent,
        },
        checklist,
        allComplete,
        dismissedAt: null, // TODO: Track if user dismissed onboarding
      },
      account: {
        name: account.name,
        createdAt: account.created_at,
      },
    });
  } catch (error: any) {
    console.error('[onboarding] Error:', error);
    return NextResponse.json(
      { error: 'Failed to get onboarding status', code: 'INTERNAL_ERROR' },
      { status: 500 }
    );
  }
}

export async function PUT(request: NextRequest) {
  const authResult = await requireAuth(request);
  if (authResult.response) {
    return authResult.response;
  }

  const { apiKey } = authResult;
  const accountId = apiKey.userId;

  if (!accountId) {
    return NextResponse.json(
      { error: 'No account associated with this API key', code: 'NO_ACCOUNT' },
      { status: 400 }
    );
  }

  try {
    const body = await request.json();
    const { stepId, dismiss } = body;

    const db = getDb();

    if (dismiss) {
      // User wants to dismiss the onboarding checklist
      await db.query(
        `INSERT INTO onboarding_progress (account_id, step_id, completed_at)
         VALUES ($1, 'dismissed', NOW())
         ON CONFLICT (account_id, step_id) DO NOTHING`,
        [accountId]
      );

      return NextResponse.json({
        success: true,
        message: 'Onboarding dismissed',
      });
    }

    if (!stepId) {
      return NextResponse.json(
        { error: 'stepId is required', code: 'VALIDATION_ERROR' },
        { status: 400 }
      );
    }

    // Validate step ID
    const validStep = ONBOARDING_STEPS.find((s) => s.id === stepId);
    if (!validStep) {
      return NextResponse.json(
        { error: 'Invalid step ID', code: 'INVALID_STEP' },
        { status: 400 }
      );
    }

    // Mark step as complete
    await db.query(
      `INSERT INTO onboarding_progress (account_id, step_id, completed_at)
       VALUES ($1, $2, NOW())
       ON CONFLICT (account_id, step_id) DO NOTHING`,
      [accountId, stepId]
    );

    return NextResponse.json({
      success: true,
      message: `Step "${validStep.title}" marked as complete`,
      stepId,
    });
  } catch (error: any) {
    console.error('[onboarding] Error:', error);
    return NextResponse.json(
      { error: 'Failed to update onboarding', code: 'INTERNAL_ERROR' },
      { status: 500 }
    );
  }
}

// Helper functions

function isWithinDays(dateStr: string, days: number): boolean {
  const date = new Date(dateStr);
  const now = new Date();
  const diffMs = now.getTime() - date.getTime();
  const diffDays = diffMs / (1000 * 60 * 60 * 24);
  return diffDays <= days;
}

async function checkAutoCompleteConditions(
  db: any,
  accountId: string
): Promise<Record<string, boolean>> {
  const checks: Record<string, boolean> = {
    account_created: true, // Always true if we got here
  };

  // Check if user has created any endpoints
  try {
    const endpointsResult = await db.query(
      `SELECT COUNT(*) as count FROM endpoints WHERE created_by = $1`,
      [accountId]
    );
    checks.first_endpoint = parseInt(endpointsResult.rows[0]?.count || '0', 10) > 0;
  } catch {
    checks.first_endpoint = false;
  }

  // Check if user has any usage events (made API calls)
  try {
    const usageResult = await db.query(
      `SELECT COUNT(*) as count FROM usage_events WHERE account_id = $1 LIMIT 1`,
      [accountId]
    );
    checks.test_request = parseInt(usageResult.rows[0]?.count || '0', 10) > 0;
  } catch {
    checks.test_request = false;
  }

  // view_usage is auto-complete if they have usage events
  checks.view_usage = checks.test_request;

  return checks;
}

